/********************************
 * Grid CSS
 ********************************/
.grid-container {
	display: grid;
	grid-column-gap: 20px;
	grid-template-columns: auto;
	background-color: #2196f3; /* developpment purposes*/
	padding: 10px;
}

.grid-item {
	background-color: rgba(255, 255, 255, 0.8); /* developpment purposes*/
	border: 1px solid rgba(0, 0, 0, 0.8); /* developpment purposes*/
	padding: 20px;
	font-size: 30px;
	text-align: center;
}
