/********************************
 * Icons
 ********************************/
@font-face {
	font-family: "icomoon";
	src: url("/fonts/icomoon.eot?whvtr9");
	src: url("/fonts/icomoon.eot?whvtr9#iefix") format("embedded-opentype"),
		url("/fonts/icomoon.ttf?whvtr9") format("truetype"),
		url("/fonts/icomoon.woff?whvtr9") format("woff");
	font-weight: normal;
	font-style: normal;
	font-display: block;
}

.iconBadge {
	display: flex;
	justify-content: center;
	align-items: center;
	color: var(--color-white);
	border-radius: 50%;
	width: 50px;
	height: 50px;
	font-size: var(--fontsize-s);
	box-shadow: var(--boxshadow-second-blurred);
}

.iconBadge--small {
	width: 30px;
	height: 30px;
}
.iconBadge--false {
	background: var(--color-contrast);
}

.iconBadge--true {
	background: var(--color-secondary);
}

.iconBadge--unanswered {
	background: var(--color-disable);
}

[class^="icon-"],
[class*=" icon-"] {
	/* use !important to prevent issues with browser extensions that change fonts */
	font-family: "icomoon" !important;
	speak: never;
	font-style: normal;
	font-weight: normal;
	font-variant: normal;
	text-transform: none;
	line-height: 1;
	text-decoration: none;

	/* Better Font Rendering =========== */
	-webkit-font-smoothing: antialiased;
	-moz-osx-font-smoothing: grayscale;
}

